import axios from 'axios'
const isBrowser = typeof window !== 'undefined'
const initialPosition = {
  coords: {
    accuracy: 0,
    altitude: null,
    altitudeAccuracy: null,
    heading: null,
    latitude: 0,
    longitude: 0,
    speed: null
  },
  timestamp: 0
}
const initialLocation = {
  lat: '',
  lon: '',
  address: {
    country_code: ''
  }
}
const initialPositionOptions = {
  enableHighAccuracy: true,
  maximumAge: 30000,
  timeout: 27000
}

export class Localizer {
  private navigator: NavigatorObject = {}
  public position: Position = initialPosition
  public locale: string = ''
  public userAgent: string = ''
  public ip: string = ''
  public location: Location = initialLocation
  private positionOptions: PositionOptions = initialPositionOptions

  constructor(config: LocalizerConfig = {}) {
    this.locale = config.defaultLocale || 'en-US'
    if (isBrowser) {
      this.navigator = config.navigator || window.navigator
    }
    if (config.positionOptions) {
      this.positionOptions = config.positionOptions
    }
  }

  async getPosition(): Promise<Position> {
    try {
      if (!isBrowser || !(this.navigator && this.navigator.geolocation)) {
        throw new Error('Cannot get position outside of browser')
      }

      await this.navigator.geolocation.getCurrentPosition(
        this.returnPositionCallback(this),
        this.positionErrorCallback,
        this.positionOptions
      )

      return this.position
    } catch (error) {
      throw new Error(
        `Error: Navigator unable to retrieve position. \n ${error}`
      )
    }
  }

  /**
   * getLocation
   * --
   * Policy:
   * https://operations.osmfoundation.org/policies/nominatim/
   * --
   * @param lat
   * @param lon
   * @return {Location}
   */
  async getLocation(lat: number, lon: number): Promise<Location> {
    if (lat === undefined && lon === undefined && this.position.timestamp > 0) {
      lat = this.position.coords.latitude
      lon = this.position.coords.longitude
    }

    try {
      const urlBase = `https://nominatim.openstreetmap.org/reverse`
      const query = `?format=json&lat=${lat}&lon=${lon}`
      const { data } = await axios.get(`${urlBase}${query}`)
      this.location = data

      return data
    } catch (error) {
      throw new Error(
        `Error: Nomination unable to retrieve location. \n ${error}`
      )
    }
  }

  async getIp(): Promise<IpData> {
    try {
      const { data } = await axios.get(
        'https://www.cloudflare.com/cdn-cgi/trace'
      )
      const parsedData = this.parsePlainTextData(data)
      this.ip = parsedData.ip
      this.location.address.country_code = parsedData.loc.toLowerCase()
      this.userAgent = parsedData.uag

      return parsedData
    } catch (error) {
      throw new Error(`Error: Cloudflare unable to retrieve IP. \n ${error}`)
    }
  }

  getLocale(): string {
    if (!isBrowser || !(this.navigator && this.navigator.languages)) {
      throw new Error('Cannot get locale outside of browser')
    }
    this.locale = this.navigator.languages[0]
    return this.locale
  }

  getUserAgent(): string {
    if (!isBrowser || !(this.navigator && this.navigator.userAgent)) {
      throw new Error('Cannot get useragent outside of browser')
    }
    this.userAgent = this.navigator.userAgent
    return this.userAgent
  }

  private returnPositionCallback = (localizer: Localizer): PositionCallback => {
    return (newPosition: Position): void => {
      localizer.position = newPosition
    }
  }

  private positionErrorCallback: PositionErrorCallback = (
    error: PosistionError
  ): void => {
    console.log(error)
  }

  /**
   * parsePlainTextData
   * ---
   * Converts plain text data to object
   * ---
   * @param plainText - Expects ~ 'ip=98.176.246.69\nloc=US`
   *
   * Adpoted from this answer:
   * https://stackoverflow.com/questions/17774266/read-env-file-and-convert-all-lines-into-an-object#answer-17774334
   */
  private parsePlainTextData(plainText: string): StringIndexer {
    const data: StringIndexer = {}
    plainText.replace(
      /(\w+)=(.+)/g,
      (_: string, key: string, value: string) => {
        return (data[key] = value)
      }
    )
    return data
  }
}

type StringIndexer = { [prop: string]: string }

interface LocalizerConfig {
  defaultLocale?: string
  navigator?: Navigator
  positionOptions?: PositionOptions
}

interface PositionOptions {
  enableHighAccuracy?: boolean
  maximumAge?: number
  timeout?: number
}

interface PosistionError {
  code: number
  message: string
}

/* eslint-disable camelcase */
interface IpData {
  fl?: string
  h?: string
  ip?: string
  ts?: string
  visit_scheme?: string
  uag?: string
  colo?: string
  http?: string
  loc?: string
  tls?: string
  sni?: string
  warp?: string
}

interface Location {
  place_id?: number
  licence?: string
  osm_type?: string
  osm_id?: number
  lat: string
  lon: string
  display_name?: string
  address: {
    road?: string
    neighbourhood?: string
    hamlet?: string
    city?: string
    county?: string
    state?: string
    postcode?: string
    country?: string
    country_code: string
  }
  boundingbox?: [string, string, string, string]
}
/* eslint-enable */

interface NavigatorObject {
  geolocation?: any
  languages?: readonly string[]
  userAgent?: string
}
